// File: src/components/Profile.js
import React, { useState, useEffect } from "react";
import axios from "axios";
import Header from "../components/Header";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import BackToTop from "../components/BackToTop";
import { motion, useAnimation } from "framer-motion";
import { Button, Container } from "react-bootstrap";
import { useNavigate, Link, useParams } from "react-router-dom";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import Table from "react-bootstrap/Table";

const Profile=()=> {

const navigate = useNavigate();
 
  const { userID } = useParams();
  const [user, setUser] = useState([]);

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = await axios.get(
          `http://localhost:5020/v1/api/admin/lay-thong-tin-user/${userID}`
        );
        setUser(response.data);
      } catch (error) {
        console.error("Li khi ly thng tin ngi dng:", error);
      }
    };

    fetchUser();
  }, [userID]);
  const handleEdit = (userID) => {
    // X l chc nng sa  y, v d: chuyn hng n trang sa thng tin ngi dng
    console.log(`Sa thng tin ca user c ID: ${userID}`);
    navigate(`/profile/editprofile/${userID}`);
  };
   

 const [orders, setOrders] = useState([]);

 useEffect(() => {
   fetchOrdersByUser();
 }, [userID]);

 const fetchOrdersByUser = async () => {
   try {
     const response = await axios.get(
       `http://localhost:5020/v1/api/admin/api/orders/user/${userID}`
     );
     setOrders(response.data);
   } catch (error) {
     console.error("Error fetching orders by user:", error);
   }
 };
    const formatCurrency = (price) => {
      // Chuyn i gi tr DECIMAL(10) sang s nguyn
      const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

      // S dng hm toLocaleString  nh dng gi theo kiu VN
      const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
        currency: "VND",
      });

      // Thm ch "VN" vo cui chui
      return formattedPrice + " VN";
    };
  const getStatusColor = (status) => {
    switch (status) {
      case "Tip nhn":
        return "yellow";
      case " thanh ton":
        return "#99FF99"; // Mu cam cho trng thi 'Pending'
      // Mu xanh cho trng thi 'Confirmed'
      case " hu":
        return "#FF6633";
      case " kt thc":
        return "#FF9900";
      // Mu  cho trng thi 'Cancelled'
      default:
        return "black"; // Mu mc nh nu trng thi khng khp vi bt k trng hp no
    }
  };
  return (
    <>
      <BackToTop />
      <ToastContainer />
      <Header />
      <div className="boxGioiThieu"></div>
      <motion.div
        initial={{ opacity: 0, y: 98 }}
        animate={{ opacity: 1, y: 1 }}
        transition={{ type: "spring", duration: 0.6 }}
      >
        <Container>
          <div className="mt-4 ">
            <h1 className="text-center my-5 ">TI KHON</h1>

            <Tabs
              defaultActiveKey="Chng trnh tour"
              id="fill-tab-example"
              className="mb-3 nav-linkk back"
              fill
            >
              <Tab eventKey="Chng trnh tour" title="H S C NHN">
                <h3 className="text-danger  pt-3">THNG TIN C NHN</h3>
                {user ? (
                  <div className="pt-3  pb-5">
                    <Row className="my-4">
                      <Col>
                        <h4>
                          Username:{" "}
                          <span className="text-primary">{user.username}</span>
                        </h4>
                      </Col>
                      <Col>
                        {" "}
                        <h4>
                          Email:{" "}
                          <span className="text-primary">{user.email}</span>
                        </h4>
                      </Col>
                    </Row>
                    <Row className="my-4">
                      <Col>
                        <h4>
                          H v tn:{" "}
                          <span className="text-primary">{user.fullname}</span>
                        </h4>
                      </Col>
                      <Col>
                        {" "}
                        <h4>
                          S in thoi:{" "}
                          <span className="text-primary">{user.phone}</span>
                        </h4>
                      </Col>
                    </Row>

                    <h4 className="my-4 mb-5">
                      a ch:{" "}
                      <span className="text-primary">{user.address}</span>
                    </h4>
                    <hr />

                    <Button
                      className="btn-warning col-3 p-3 my-4 fs-5 fw-bold "
                      onClick={() => handleEdit(user.id)}
                    >
                      Chnh sa thng tin
                    </Button>

                    {/* Hin th cc thng tin c nhn khc cn thit */}
                  </div>
                ) : (
                  <p>Loading...</p>
                )}
              </Tab>

              <Tab eventKey="Quanlydondattou" title="N T TOUR">
                <h3 className="text-danger pt-3">DANH SCH N T TOUR</h3>

                <Table striped bordered hover className=" mt-4">
                  <thead>
                    <tr>
                      <th>M Booking</th>
                      <th>Hnh nh Tour</th>
                      <th>Tn Tour</th>
                      <th>Thi gian</th>

                      <th>SL t Ch</th>
                      <th>Ngy t</th>
                      <th>Tng gi</th>
                      <th>Trng thi</th>
                    </tr>
                  </thead>
                  <tbody>
                    {orders.map((order) => (
                      <tr key={order.OrderID}>
                        <td>{order.id}</td>
                        <td>
                          {" "}
                          <img
                            src={
                              order.image
                                ? `data:image/jpeg;base64,${order.image.toString(
                                    "base64"
                                  )}`
                                : ""
                            }
                            alt="Tour"
                            style={{ width: "150px", height: "100px" }}
                          />
                        </td>
                        <td>{order.name}</td>
                        <td>
                          {new Date(order.start_date).toLocaleDateString()} -{" "}
                          {new Date(order.end_date).toLocaleDateString()}
                        </td>
                        <td className="lh-lg">
                          {" "}
                          <span className="fw-bold">S ngi ln: </span>
                          {order.quantity}
                          <br />
                          <span className="fw-bold">S tr em: </span>
                          {order.child_quantity}
                          <br />
                          <span className="fw-bold">S tr nh: </span>
                          {order.infant_quantity}
                          <br />
                        </td>
                        <td>
                          {new Date(order.booking_date_time).toLocaleDateString(
                            "vi-VN",
                            {
                              year: "numeric",
                              month: "numeric",
                              day: "numeric",
                              hour: "numeric",
                              minute: "numeric",
                              second: "numeric",
                            }
                          )}
                        </td>
                        <td className="text-danger fw-bold">
                          {formatCurrency(order.total_price)}
                        </td>
                        <td
                          style={{
                            backgroundColor: getStatusColor(order.status),
                          }}
                        >
                          {order.status}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </Table>
                <Row className="mb-5">
                  <Col></Col>
                  <Col className="col-7">
                    <p className="text-danger fs-5">
                      Khch hng c nhu cu hu tour vui lng lin h n
                      Hotline  kim tra v hu tour
                    </p>
                  </Col>
                </Row>
              </Tab>
            </Tabs>
          </div>
        </Container>
      </motion.div>
    </>
  );
}

export default Profile;
