import SIDEBAR from "./componentss/sidebar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Header1 from "./componentss/Header1";
import React, { useEffect, useState } from "react";
import { useAuth } from "./AuthContextAdmin";
import { useNavigate, Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import Table from "react-bootstrap/Table";
import { Button } from "react-bootstrap";
import Spinner from "react-bootstrap/Spinner";
const TourLietKe = () => {
     const [tours, setTours] = useState([]);
      const [loading, setLoading] = useState(true);
     useEffect(() => {
       // Gi API  ly danh sch tour t backend
       const fetchTours = async () => {
         try {
           const response = await axios.get(
             "http://localhost:5020/v1/api/admin/get-tours"
           );
           setTours(response.data);
            setLoading(false);
         } catch (error) {
           console.error("Error fetching tours:", error);
         }
       };

       fetchTours();
     }, []); // Chy mt ln khi component mount

     const handleDelete = async (tourID) => {
        try {
          // Hin th thng bo xc nhn
          const confirm = window.confirm(
            "Bn c chc chn mun xa tour ny khng?"
          );
          if (!confirm) {
            return; // Ngi dng khng xc nhn, khng thc hin xa
          }

          // Gi API  xa tour t backend
          await axios.delete(
            `http://localhost:5020/v1/api/admin/delete-tour/${tourID}`
          );

          // Cp nht li danh sch tour sau khi xa
          setTours(tours.filter((tour) => tour.id !== tourID));

          // Hin th thng bo thnh cng
          toast.success("Tour  c xa thnh cng.");
        } catch (error) {
          console.error("Li khi xa tour:", error);

          // Hin th thng bo li
          toast.error("Tour  c Khch hng t.");
        }
     };

     const handleEdit = (tourID) => {
      navigate(`/TourDuLichTravel/admin/sua-tour/${tourID}`);
     };
   




  const { adminToken } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    // Kim tra nu ngi dng cha ng nhp, chuyn hng v trang login
    if (!adminToken) {
      navigate("/TourDuLichTravel/admin");
    }
  }, [adminToken, navigate]);

  if (!adminToken) {
    // Nu cha ng nhp, khng hin th ni dung ca trang
    return null;
  }
 const formatCurrency = (price) => {
   // Chuyn i gi tr DECIMAL(10) sang s nguyn
   const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

   // S dng hm toLocaleString  nh dng gi theo kiu VN
   const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
     currency: "VND"
   });

   // Thm ch "VN" vo cui chui
   return formattedPrice + " VN";
 };

let index=1;

  return (
    <>
      <ToastContainer />
      <Header1 />
      <div className="boxGioiThieu"></div>
      <Row className="d-flex flex-row">
        <Col className="col-2">
          <SIDEBAR />
        </Col>
        <Col className="col-10 ">
          <div className="ms-5 ps-3 mt-3">
            <h2>Tour List</h2>
            <Row className="d-flex flex-row">
              <Col className="col-11"></Col>
              <Col className="">
                <Link to="/TourDuLichTravel/admin/TourThemMoi">
                  <Button className="col-10 ms-2">Thm</Button>
                </Link>
              </Col>
            </Row>
            <br />
            {loading ? (
              <h2>
                {" "}
                <Spinner animation="grow" variant="dark" />
                Loading...
              </h2>
            ) : (
              <Table striped bordered hover>
                <thead>
                  <tr>
                    <th>STT</th>

                    <th>Tn TOUR</th>
                    <th>Hng dn vin</th>

                    <th>Ngy bt u</th>
                    <th>Ngy kt thc</th>
                    <th>Gi ngi ln</th>
                    <th>Gi tr em</th>
                    <th>Gi tr nh</th>
                    <th>S ch cn</th>
                    <th>M t</th>
                    <th>Hnh nh</th>
                    <th>Xo</th>
                    <th>Sa</th>
                  </tr>
                </thead>
                <tbody>
                  {tours.map((tour) => (
                    <tr key={tour.TourID}>
                      <td>{index++}</td>
                      <td>{tour.name}</td>
                      <td>
                        <ul>
                          {tour.guides.map((guide, index) => (
                            <li key={index}>{guide}</li>
                          ))}
                        </ul>
                      </td>

                      <td>{new Date(tour.start_date).toLocaleDateString()}</td>
                      <td>{new Date(tour.end_date).toLocaleDateString()}</td>
                      <td className="text-danger fw-bold">
                        {formatCurrency(tour.price)}
                      </td>
                      <td className="text-danger fw-bold">
                        {formatCurrency(tour.child_price)}
                      </td>
                      <td className="text-danger fw-bold">
                        {formatCurrency(tour.infant_price)}
                      </td>
                      <td>{tour.quantity}</td>
                      <td>{tour.description}</td>
                      <td>
                        {/* Hin th hnh nh, gi s nh c lu di dng base64 */}
                        <img
                          src={
                            tour.image
                              ? `data:image/jpeg;base64,${tour.image.toString(
                                  "base64"
                                )}`
                              : ""
                          }
                          alt="Tour"
                          style={{ width: "150px", height: "100px" }}
                        />
                      </td>

                      <td>
                        <Button
                          onClick={() => handleDelete(tour.id)}
                          className="btn-danger col-12"
                        >
                          Delete
                        </Button>
                      </td>
                      <td>
                        <Button
                          onClick={() => handleEdit(tour.id)}
                          className="btn-warning col-12"
                        >
                          Edit
                        </Button>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </Table>
            )}
          </div>
        </Col>
      </Row>
    </>
  );
};
export default TourLietKe;
