import SIDEBAR from "./componentss/sidebar";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Header1 from "./componentss/Header1";
import React, { useEffect, useState } from "react";
import { useAuth } from "./AuthContextAdmin";
import { useNavigate, Link, useParams } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import axios from "axios";
import Table from "react-bootstrap/Table";
import Container from "react-bootstrap/Container";
import { Button } from "react-bootstrap";
import Spinner from "react-bootstrap/Spinner";
import Form from "react-bootstrap/Form";



const OrderDetails = () => {
const { orderID } = useParams();
const [orderDetails, setOrderDetails] = useState({});
  const [newStatus, setNewStatus] = useState("");
useEffect(() => {
  // Fetch order details when the component mounts
  fetchOrderDetails();
}, [orderID]);

const fetchOrderDetails = async () => {
  try {
    const response = await axios.get(
      `http://localhost:5020/v1/api/admin/api/orders/${orderID}`
    );
    setOrderDetails(response.data);
  } catch (error) {
    console.error("Error fetching order details:", error);
  }
};

  const { adminToken } = useAuth();
  const navigate = useNavigate();

  useEffect(() => {
    // Kim tra nu ngi dng cha ng nhp, chuyn hng v trang login
    if (!adminToken) {
      navigate("/TourDuLichTravel/admin");
    }
  }, [adminToken, navigate]);

  if (!adminToken) {
    // Nu cha ng nhp, khng hin th ni dung ca trang
    return null;
  }
   const formatCurrency = (price) => {
     // Chuyn i gi tr DECIMAL(10) sang s nguyn
     const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

     // S dng hm toLocaleString  nh dng gi theo kiu VN
     const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
       currency: "VND",
     });

     // Thm ch "VN" vo cui chui
     return formattedPrice+" VN";
   };


   const handleStatusChange = (e) => {
     setNewStatus(e.target.value);
   };

   const handleUpdateStatus = async () => {
     try {
       // Send a request to update the status
       await axios.put(
         `http://localhost:5020/v1/api/admin/api/orders/${orderID}/status`,
         {
           status: newStatus,
         }
       );
       // Refetch order details to update the displayed information
       fetchOrderDetails();
       toast.success("Cp nht trng thi thnh cng", {
         position: toast.POSITION.TOP_RIGHT,
       });
       setTimeout(() => {
         navigate("/TourDuLichTravel/admin/OrderList");
       }, 1500);

    
     } catch (error) {
       console.error("Error updating status:", error);
       toast.error("Error updating status", {
         position: toast.POSITION.TOP_RIGHT,
       });
     }
   };

  return (
    <>
      <ToastContainer />
      <Header1 />
      <div className="boxGioiThieu"></div>
      <Row className="d-flex flex-row">
        <Col className="col-2">
          <SIDEBAR />
        </Col>
        <Col className="col-10 ">
          <div className="ms-5 ps-3 mt-3">
            <h2>Order Details</h2>
            <Table striped bordered hover className="my-5">
              <thead>
                <tr>
                  <th>Tn Khch Hng</th>
                  <th>Email</th>
                  <th>S in thoi</th>
                  <th>a ch</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{orderDetails.fullname}</td>
                  <td>{orderDetails.email}</td>
                  <td>{orderDetails.phone}</td>
                  <td>{orderDetails.address}</td>
                </tr>
              </tbody>
            </Table>
            <hr />
            <Table striped bordered hover className="my-5">
              <thead>
                <tr>
                  <th>M Tour</th>
                  <th>Tn Tour</th>
                  <th>Ngy bt u</th>
                  <th>Ngy kt thc</th>
                  <th>SL t ch</th>
                  <th>Thi gian t</th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>{orderDetails.id}</td>
                  <td>{orderDetails.name}</td>
                  <td className="text-primary fw-bold">
                    {" "}
                    {new Date(orderDetails.start_date).toLocaleDateString()}
                  </td>
                  <td className="text-primary fw-bold">
                    {" "}
                    {new Date(orderDetails.end_date).toLocaleDateString()}
                  </td>
                  <td className="lh-lg">
                    {" "}
                    <span className="fw-bold">S ngi ln: </span>
                    {orderDetails.quantity}
                    <br />
                    <span className="fw-bold">S tr em: </span>
                    {orderDetails.child_quantity}
                    <br />
                    <span className="fw-bold">S tr nh: </span>
                    {orderDetails.infant_quantity}
                    <br />
                  </td>
                  <td>
                    {new Date(
                      orderDetails.booking_date_time
                    ).toLocaleDateString("vi-VN", {
                      year: "numeric",
                      month: "numeric",
                      day: "numeric",
                      hour: "numeric",
                      minute: "numeric",
                      second: "numeric",
                    })}
                  </td>
                </tr>
              </tbody>
            </Table>
            <Row>
              {" "}
              <Col className="col-8"></Col>
              <Col>
                <h3>
                  Tng gi:{" "}
                  <span className="text-danger fw-bold">
                    {formatCurrency(orderDetails.total_price)}
                  </span>
                </h3>
              </Col>
            </Row>{" "}
            <hr />
            <Row>
              {" "}
              <Col>
                <div>
                  <h4 htmlFor="status">Cp nht trng thi:</h4>
                  <br />

                  <Form.Select
                    aria-label="Default select example"
                    id="status"
                    value={newStatus}
                    onChange={handleStatusChange}
                  >
                    <option>Open this select menu</option>
                    <option value="Tip nhn">Tip nhn</option>
                    <option value=" thanh ton"> thanh ton</option>
                    <option value=" hu"> hu</option>
                    <option value=" kt thc"> kt thc</option>
                  </Form.Select>
                </div>
                <br />
                <div>
                  <Button onClick={handleUpdateStatus}>Cp nht</Button>
                </div>
              </Col>
              <Col></Col>
            </Row>
          </div>
        </Col>
      </Row>
    </>
  );
};
export default OrderDetails;
