import { Container } from "@mui/system";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { NavLink, useNavigate } from "react-router-dom";
import { motion, useAnimation } from "framer-motion";
import React, { useEffect, useState } from "react";
import axios from "axios";
import { useAuth } from "./AuthContext";
import Header from "../components/Header";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import BackToTop from "../components/BackToTop";
import { useParams } from "react-router-dom";
import { Link } from "react-router-dom";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import "../assets/scss/TourMienTay3Ngay2Dem.scss";
import Accordion from "react-bootstrap/Accordion";
import picture1 from "../assets/image/picture-1.png";
import picture2 from "../assets/image/picture-2.png";
import picture3 from "../assets/image/picture-3.png";
import picture4 from "../assets/image/picture-4.png";
import picture5 from "../assets/image/picture-5.png";
import picture6 from "../assets/image/picture-6.png";
import picture7 from "../assets/image/picture-7.png";
import Lightbox from "yet-another-react-lightbox";
import Spinner from "react-bootstrap/Spinner";
import Table from "react-bootstrap/Table";
// Import useHistory

const TourDetail = () => {
  const { tourID, userID } = useParams();
    const { user, userId } = useAuth();
  console.log("ID:", tourID);
  console.log("user:", user);
  const [tour, setTour] = useState([]);
  const [images, setImages] = useState([]);
   const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchTour = async () => {
      try {
        const responseTour = await axios.get(
          `http://localhost:5020/v1/api/admin/lay-thong-tin-tour/${tourID}`
        );
        const responseImages = await axios.get(
          `http://localhost:5020/v1/api/admin/lay-hinh-anh-tour/${tourID}`
        );

        setTour(responseTour.data);
        setImages(responseImages.data);
        setLoading(false);
      } catch (error) {
        console.error("Li khi ly thng tin tour v hnh nh:", error);
      }
    };

    fetchTour();
  }, [tourID]);
    const navigate = useNavigate();

  const handleEdit = (tourID) => {
     if (!user) {
       console.error("User not logged in");
       navigate("/Login");
       return;
     }
    navigate(`/booking-tour/${tourID}`);
  };
   const [open, setOpen] = React.useState(false);
   const handleOpen = () => setOpen(true);
   const handleCloseOpen = () => setOpen(false);
   const slides = [
     { src: picture1 },
     { src: picture2 },
     { src: picture3 },
     { src: picture4 },
     { src: picture5 },
     { src: picture6 },
     { src: picture7 },
   ];
  const formatCurrency = (price) => {
    // Chuyn i gi tr DECIMAL(10) sang s nguyn
    const priceInteger = Math.round(price * 100); // Gi s 2 ch s thp phn

    // S dng hm toLocaleString  nh dng gi theo kiu VN
    const formattedPrice = (priceInteger / 100).toLocaleString("vi-VN", {
      currency: "VND",
    });

    // Thm ch "VN" vo cui chui
    return formattedPrice + " VN";
  };
  return (
    <>
      <BackToTop />
      <ToastContainer />
      <Header />
      <div className="boxGioiThieu"></div>
      <motion.div
        initial={{ opacity: 0, y: 98 }}
        animate={{ opacity: 1, y: 1 }}
        transition={{ type: "spring", duration: 0.6 }}
      >
        {loading ? (
          <h2>
            {" "}
            <Spinner animation="grow" variant="dark" />
            Loading...
          </h2>
        ) : (
          <Container className="mt-5 mx-auto  ">
            <div className="  my-md-5 mt-2 mb-3">
              <Row className="d-flex flex-column">
                <Col className="col-10 col-md-12">
                  <h2 className="text-start text-info">{tour.name}</h2>
                </Col>
                <Col className="col-10 col-md-8">
                  <p className="text-start fs-5">
                    Gi tour gc/khch:{" "}
                    <span className="text-warning">
                      {" "}
                      {formatCurrency(tour.price)}
                    </span>
                  </p>

                  <p className="text-dark"> S ch cn: {tour.quantity}</p>
                </Col>

                <Col className="col-10 col-md-8">
                  <Button variant="warning" onClick={() => handleEdit(tour.id)}>
                    T NGAY
                  </Button>{" "}
                </Col>
              </Row>
            </div>
            <div>
              <Tabs
                defaultActiveKey="Chng trnh tour"
                id="fill-tab-example"
                className="mb-3 nav-linkk back"
                fill
              >
                <Tab eventKey="Chng trnh tour" title="Chng trnh tour">
                  <h3 className="text-dark">CHUONG TRNH TOUR</h3>
                  <Row className="d-flex flex-row my-md-5 my-3">
                    <Col className="col-md-6 col-12 mx-md-2 mx-0 mb-md-0 mb-3">
                      {images.length > 0 && (
                        <img
                          src={`data:image/jpeg;base64,${images[0]}`}
                          alt="First Image"
                          style={{
                            width: "100%",
                            height: "500px",
                            marginRight: "10px",
                          }}
                          className="rounded-4"
                        />
                      )}
                    </Col>
                    <Col className="mx-2">
                      <Accordion defaultActiveKey={["0", "1", "2"]} alwaysOpen>
                        <Accordion.Item eventKey="0">
                          <Accordion.Header>
                            {" "}
                            <h4>
                              <span className="text-dark fw-bold">
                                M T CHI TIT TOUR:
                              </span>{" "}
                            </h4>
                          </Accordion.Header>
                          <Accordion.Body className="">
                            <Row>
                              <Col>
                                {" "}
                                <p className="text-dark fw-bold">
                                  {" "}
                                  Ngy bt u:{" "}
                                  <span className="text-danger fw-bold">
                                    {new Date(
                                      tour.start_date
                                    ).toLocaleDateString()}
                                  </span>
                                </p>
                              </Col>
                              <Col>
                                <p className="text-dark fw-bold">
                                  {" "}
                                  Ngy kt thc:{" "}
                                  <span className="text-danger fw-bold">
                                    {new Date(
                                      tour.end_date
                                    ).toLocaleDateString()}
                                  </span>
                                </p>
                              </Col>
                            </Row>

                            <p>{tour.description}</p>
                          </Accordion.Body>
                        </Accordion.Item>
                      </Accordion>
                    </Col>
                  </Row>
                </Tab>
                <Tab eventKey="Bng gi tour" title="Bng gi tour">
                  <h3 className="text-dark">BNG GI TOUR</h3>
                  <Table striped bordered hover>
                    <thead>
                      <tr>
                       
                        <th>Ngi ln(Trn 11 tui) </th>
                        <th>Tr em(4 - 11 tui) </th>
                        <th>Tr nh({"<"} 4 tui) </th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td className="text-danger fw-bold">
                          {" "}
                          {formatCurrency(tour.price)}
                        </td>
                        <td className="text-danger fw-bold">
                          {" "}
                          {formatCurrency(tour.child_price)}
                        </td>
                        <td className="text-danger fw-bold">
                          {" "}
                          {formatCurrency(tour.infant_price)}
                        </td>
                      </tr>
                    </tbody>
                  </Table>
                </Tab>

                <Tab eventKey="iu kin tour" title="iu kin tour">
                  <h3 className="text-start ">IU KIN TOUR</h3>{" "}
                  <div className="my-3 pb-md-5">
                    <p className="lh-sm fw-lighter">
                      <span className="fw-bold">Tour bao gm:</span>
                      <br />- Xe du lch i mi my lnh.
                      <br />- HDV chuyn nghip sut tuyn (tour ghp vi ngi
                      nc ngoi).
                      <br />- n cm phn 4 -5, mn 3 ba tra, 2 ba n ti +
                      n 2 ba sng ti khch sn.
                      <br />- Tu tham quan trn sng (c trang b o phao) -
                      Bn Tre + ch ni + lng b + Tr S, xung cho, tri
                      cy, tr mt ong, n ca ti t, xe nga.
                      <br />- Bo him du lch 20.000.000/v.
                      <br />- Khch sn tiu chun, 2-3 khch/phng.
                      <br />- Nc sui 1 chai/ngi.
                      <br />
                      <span className="fw-bold">Tour khng bao gm:</span>
                      <br />- Ph thu phng n 200.000 vnd/khch (KS 2 sao),
                      300.000 vnd/khch (KS 3 sao).
                      <br />- Chi ph tham quan ngoi chng trnh.
                      <br />- Chi ph n ung ngoi chng trnh.
                      <br />- Thu VAT 08 %.
                      <br />
                      <span className="fw-bold">iu kin tr em:</span>
                      <br />- Tr em di 4 tui c min ph v (ngi chung
                      vi ngi ln).
                      <br />- Tr t 4-10 tui tnh 75% gi v ngi ln.
                      <br />- Tr trn 10 tui tnh gi ngi ln.
                      <br />
                      <span className="fw-bold fst-italic">Lu : </span>
                      <span className="fst-italic">
                        2 ngi ln ch c km 1 tr em. T b th 2 tnh gi
                        nh ngi ln.
                      </span>
                      <br />
                      <span className="fw-bold">Quy nh hy tour:</span>
                      <br />
                      Trng hp hy khng bo trc hoc n tr gi khi hnh,
                      qu khch phi chu pht 100% ph tour.
                      <br />
                      <span className="fw-bold">
                        Quy nh hy i vi ngy l, tt
                      </span>
                      <br />
                      - Hy trc 07 ngy khi hnh chu pht 50% ph tour.
                      <br />- Hy trc 03-09 ngy khi hnh chu pht 75% ph
                      tour.
                      <br />- Hy trc 02 ngy khi hnh chu pht 100 % ph
                      tour.
                      <br />
                      <span className="fw-bold">
                        Quy nh hy i vi ngy thng
                      </span>
                      <br />
                      - Hy trc 07 ngy khi hnh chu pht 50% ph tour.
                      <br />- Hy trc 03-07 ngy khi hnh chu pht 75% ph
                      tour.
                      <br />- Hy trc 02 ngy khi hnh chu pht 100 % ph
                      tour.
                      <br />
                      <span>
                        <span className="fw-bold">Lu :</span> Tour Bn Phng
                        khng chu trch nhim v mi yu cu hy tour khi cha
                        nhn v xc nhn li vi qu khch. Trong trng hp ny
                        s p dng ph hy hoc b tour.
                      </span>
                    </p>
                  </div>
                </Tab>
                <Tab eventKey="Hnh nh tour" title="Hnh nh tour">
                  <Lightbox
                    open={open}
                    close={handleCloseOpen}
                    slides={slides}
                    className="d-block w-100 "
                  />
                  <h3 className="text-start ">HNH NH</h3>{" "}
                  <Row className="row-cols-4">
                    {images.map((base64Image, index) => (
                      <Col className="">
                        <img
                          className="rounded-4"
                          key={index}
                          src={`data:image/jpeg;base64,${base64Image}`}
                          alt={`Image ${index}`}
                          style={{
                            width: "100%",
                            height: "250px",
                            padding: "5px",
                          }}
                        />
                      </Col>
                    ))}
                  </Row>
                </Tab>
              </Tabs>
            </div>
          </Container>
        )}
      </motion.div>
    </>
  );
};
export default TourDetail;
